import CardsItem from '../cardsItem/CardsItem';
import './cards.scss';

const Cards = () => {

    const data = {
        'cards': [
            {
                "id": 1,
                "title": '',
                'subtitle': '  ',
                "taste": ' -',
                'portions': 10,
                'portionsDescr': '',
                'gift': '',
                'giftDescr': '  ',
                "weight": '0,5',
                "buyDescr": " ?  ,",
                "hoverText": "  ?",
                "descrTaste": " ?  ",
                "activeTaste": "    .",
                "disBuyDescr": ",  - .",
                "disabledCard": false,
                "buy": "."
            },
            {
                "id": 2,
                "title": '',
                'subtitle': '  ',
                "taste": ' ',
                'portions': 40,
                'portionsDescr': '',
                'gift': 2,
                'giftDescr': '  ',
                "weight": 2,
                "hoverText": "  ?",
                "buyDescr": " ?  ,",
                "activeTaste": "      .",
                "disBuyDescr": ",   .",
                "disabledCard": false,
                "buy": "."
            },
            {
                "id": 3,
                "title": '',
                'subtitle': '  ',
                "taste": ' ',
                'portions': 100,
                'portionsDescr': '',
                'gift': 5,
                'giftDescr': '  a',
                'giftDescrOptions': ' ',
                "weight": 5,
                "hoverText": "  ?",
                "buyDescr": " ?  ,",
                "activeTaste": "      .",
                "disBuyDescr": ",   .",
                "disabledCard": true,
                "buy": "."
            }
        ]
    }

    const render = (data) => {
        return (
            data.cards.map(({ id, title, subtitle, hoverText, activeTaste, buyDescr, disBuyDescr, disabledCard, taste, portions, portionsDescr, gift, giftDescr, giftDescrOptions, weight, buy }) => {
                return <CardsItem
                    key={id}
                    id={id}
                    title={title}
                    subtitle={subtitle}
                    hoverText={hoverText}
                    activeTaste={activeTaste}
                    buyDescr={buyDescr}
                    taste={taste}
                    disBuyDescr={disBuyDescr}
                    disabledCard={disabledCard}
                    portions={portions}
                    portionsDescr={portionsDescr}
                    gift={gift}
                    giftDescr={giftDescr}
                    giftDescrOptions={giftDescrOptions}
                    weight={weight}
                    buy={buy}
                />
            })
        )
    }

    const elements = render(data)

    return (
        <section className="cards">
            <div className="cards__mainWrapp">
                <h1 className="cards__title">   ?</h1>
                <div className="cards__wrapper">
                    {elements}
                </div>
            </div>
        </section>

    )
}

export default Cards